import React from "react";
import PropTypes from "prop-types";
import Header from "./Header";
import Inventory from "./Inventory";
import Order from "./Order";
import sampleFishes from "../sample-fishes";
import Fish from "./Fish";
import base from "../base";

class App extends React.Component {
	constructor() {
		super();
		this.state = {
			fishes: {},
			order: {},
		};
	}

	static propTypes = {
		match: PropTypes.object,
	};

	componentDidMount() {
		const { params } = this.props.match;
		//first reinstate our localStorage
		const localStorageRef = localStorage.getItem(params.storeId);
		if (localStorageRef) {
			this.setState({ order: JSON.parse(localStorageRef) });
		}
		this.ref = base.syncState(`${params.storeId}/fishes`, {
			context: this,
			state: "fishes",
		});
	}

	componentDidUpdate() {
		localStorage.setItem(this.props.match.params.storeId, JSON.stringify(this.state.order));
	}

	componentWillUnmount() {
		base.removeBinding(this.ref);
	}

	addFish = (fish) => {
		//1. Take a copy of the existing state (don't want to edit state directly)
		console.log("Adding a fish! ", fish);
		const fishes = { ...this.state.fishes };
		//2. Add our new fish to that fishes variable
		fishes[`fish${Date.now()}`] = fish;
		//3. Set the new fishes objest to state
		this.setState({
			fishes,
		});
	};

	updateFish = (key, updatedFish) => {
		// 1. take a copy of the current state
		const fishes = { ...this.state.fishes };
		// 2. update that state
		fishes[key] = updatedFish;
		// 3. set that to state
		this.setState({ fishes: fishes });
	};

	deleteFish = (key) => {
		//1. take a copy of state
		const fishes = { ...this.state.fishes };
		//2. update the state
		fishes[key] = null;
		//3. set the new state
		this.setState({ fishes });
	};

	loadSampleFishes = () => {
		this.setState({ fishes: sampleFishes });
	};

	addToOrder = (key) => {
		//1. take a copy of state
		const order = { ...this.state.order };
		//2. either add to the order or update the number in our order
		order[key] = order[key] + 1 || 1;
		//3. call setState to update our state object
		this.setState({ order });
	};

	removeFromOrder = (key) => {
		//1. take a copy of state
		const order = { ...this.state.order };
		//2. remove from order state
		delete order[key];
		//3. call setState to update our state object
		this.setState({ order });
	};

	clearAllItems = () => {
		this.setState({ order: {}, fishes: {} });
	};

	render() {
		return (
			<div className="catch-of-the-day">
				<div className="menu">
					<Header tagline="Fresh Seafood Market" />
					<ul className="fishes">
						{Object.keys(this.state.fishes).map((key) => (
							<Fish key={key} details={this.state.fishes[key]} index={key} addToOrder={this.addToOrder} />
						))}
					</ul>
				</div>
				<Order fishes={this.state.fishes} order={this.state.order} removeFromOrder={this.removeFromOrder} />
				<Inventory
					addFish={this.addFish}
					updateFish={this.updateFish}
					deleteFish={this.deleteFish}
					loadSampleFishes={this.loadSampleFishes}
					fishes={this.state.fishes}
					storeId={this.props.match.params.storeId}
					clearAllItems={this.clearAllItems}
				/>
			</div>
		);
	}
}

export default App;



